:root {
  --light: #F5F5F5;
  --light-light: #f0f0f0;
  --light-dark:#F2F2F2;
  --dark: #231F20;
  --muted: #424242;
  --muted-light: #7e7e7e;
  --muted-light-light: #b4b3b3;
  --body-color: #231F20;
  --body-bg: #F5F5F5;
  --overlay:rgba(58, 58, 58, 0.2);
  --primary: #a79600;
  --primary-dark: #162355;
  --primary-light: #7e95f0;
  --primary-light: #b1b6ce;
  --secondary: #c5e062;
  --success: #61e682;
  --accent: #e0dd21;
}

:root {
  --doc-height: 100%;
}

html {
  box-sizing: border-box;
  font-size: 100%;
  width: 100%;
  height: 100vh; /* fallback for Js load */
  height: var(--doc-height);
  margin: 0;
  padding: 0;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  width: 100%;
  margin: 0;
  padding: 0;
  background-color: white;
  display: flex;
  flex-direction: column;
  align-content: center;
  height: 100vh; /* fallback for Js load */
  height: var(--doc-height);
}

a {
  text-decoration: underline;
  color: var(--primary);
}
li {
  padding: 0.2rem;
}

.wordpress-page-area *:not(li) {
  margin-bottom: 1.7rem;
}

p {
  font-family: "Open Sans", sans-serif;
  text-align: justify;
}

h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6 {
  font-family: "Open Sans", sans-serif;
  color: var(--primary);
}

#logo1-text #logo-heading {
  color: white !important;
}

#logo1-text #logo-heading {
  color: white !important;
}

[class^=Display] {
  font-family: "Open Sans", sans-serif;
}

#welcome #welcome-display-text {
  color: white !important;
}

.section-title {
  color: var(--primary);
  font-weight: 400;
}

.section-link {
  font-size: 0.8rem;
  color: var(--dark);
}
.section-link:hover {
  color: var(--primary-light);
}

.news-tags {
  color: var(--secondary);
}

.news-title {
  color: var(--dark);
}

.news-date {
  color: var(--muted);
}

.calendar-date {
  color: var(--secondary);
}

.calendar-month {
  color: var(--dark);
}

.calendar-event {
  color: var(--primary);
}

.calendar-time {
  color: var(--muted);
}

.content-area h1, .content-area h2, .content-area h3, .content-area h4, .content-area h5, .content-area h6,
.content-area .h1, .content-area .h2, .content-area .h3, .content-area .h4, .content-area .h5, .content-area .h6 {
  color: var(--primary);
  font-weight: 500 !important;
}
.content-area h1 strong, .content-area h2 strong, .content-area h3 strong, .content-area h4 strong, .content-area h5 strong, .content-area h6 strong,
.content-area .h1 strong, .content-area .h2 strong, .content-area .h3 strong, .content-area .h4 strong, .content-area .h5 strong, .content-area .h6 strong {
  font-weight: 500;
}

/* Font awesome: Common Properties
These styles are required to make icons render reliably */
.icon::before {
  display: inline-block;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
}

#announcement-area {
  color: white;
  position: relative;
  padding-bottom: 2rem;
  background-color: var(--primary-light);
}
#announcement-area a {
  color: var(--primary);
}
#announcement-area a:hover {
  color: white;
}

#announcement-section .announcement-link {
  color: white;
}

.announcement-post-title {
  color: var(--primary);
}

.announcement-post-title-link {
  text-decoration: none;
}

.announcement-div-container-box {
  padding-top: 4rem;
}

.announcement-div-content {
  padding-top: 1rem;
}

div.announcement-post {
  background-color: white;
  margin-bottom: 3rem;
  height: fit-content;
}

.slick-arrow {
