  padding: 3rem 0 3rem 0;
  scroll-margin-top: 10rem;
}
#welcome .welcome-title {
  color: white;
}
#welcome .welcome-text-content {
  color: white;
}
#welcome .welcome-excerpt {
  margin-bottom: 2rem;
}
#welcome a {
  color: white;
}
.img-container {
  position: relative;
  width: 100%;
  aspect-ratio: 1;
}
.img-container video {
  width: 100%;
  height: auto;
}

#SideNav {
  width: 100%;
  padding-bottom: 0.1rem;
}

#SideNav .list-group.panel > .list-group-item {
  border-radius: 0;
  border: none;
}

table {
  border: none;
  width: 100%;
}
table td {
  border: none;
}
table th {
  border: none;
  padding: 0.5rem;
}
table tr {
  font-size: 0.9rem;
  vertical-align: text-top;
}

.wp-block-table td {
  border: none;
}
.wp-block-table th {
  border: none;
}
.wp-block-table tr {
  font-size: 0.9rem;
  vertical-align: text-top;
}

td {
  padding: 0.5rem;
}
td:not(:first-child) {
  border-left: var(--light) 2px solid;
}

tr:nth-child(odd) {
  background-color: var(--primary-light);
}

tr:nth-child(even) {
  background-color: var(--light-light);
}

th {
  text-align: left;
  vertical-align: text-top;
  background-color: var(--primary);
  color: white;
  font-size: 0.9rem;
  padding: 0.8rem;
  border-top: 0 !important;
  border-right: 0 !important;
  border-left: 0 !important;
  border-bottom: var(--light) 2px solid;
  font-weight: 500;
}

.wp-block-table thead {
  border: none !important;
}

.form-container {
  border: 1px var(--muted-light) solid;
  border-radius: 0.5rem;
  padding: 2rem;
}

.form-contents {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.epq-yes, .epq-no, .core-maths-yes, .core-maths-no {
  display: flex;
  align-items: center;
  gap: 0.8rem;
}

#external-applicants {
  display: none;
  flex-direction: column;
}

#internal-applicants {
  display: none;
  flex-direction: column;
}

.form-question {
  font-weight: 500;
  padding-top: 0.5rem;
}

#form-submit {
  margin-top: 2rem;
  background-color: var(--primary);
  color: white;
  border: none;
  border-radius: 0.2rem;
  padding: 0.4rem;
}

#blog-archive-main-page-area {
  margin-top: 1rem;
  margin-bottom: 3rem;
  display: flex;
  flex-direction: column;
  align-content: center;
  max-width: 65rem;
}

.search-heading {
  color: var(--primary);
  font-weight: bold;
  padding-top: 1rem;
}

div.aioseo-html-sitemap ul {
  padding: 0;
}
div.aioseo-html-sitemap li {
  background-color: var(--primary-light);
  padding: 0;
  margin-bottom: 4px;
  list-style-type: none;
}
div.aioseo-html-sitemap li:hover {
  background-color: var(--primary);
}
div.aioseo-html-sitemap li:hover a {
  color: white;
}
div.aioseo-html-sitemap li:hover > * {
  color: white;
}
div.aioseo-html-sitemap a:visited {
  color: var(--dark);
}
div.aioseo-html-sitemap a {
  color: var(--dark);
  font-weight: 500;
  padding: 0.9rem 0.9rem 0.9rem 1.8rem;
  display: flex;
  align-items: center;
  text-decoration: none;
}

div.cky-btn-revisit-wrapper.cky-revisit-bottom-left {
  backdrop-filter: blur(14px) !important;
  background-color: var(--accent) !important;
  opacity: 0.8;
  width: 30px;
  height: 30px;
}
div.cky-btn-revisit-wrapper.cky-revisit-bottom-left button:before {
  content: "*";
  padding-top: 0.35rem;
  color: white;
  display: inline-block;
  font-size: 1.5rem;
  font-weight: 900;
}
div.cky-btn-revisit-wrapper.cky-revisit-bottom-left img {
  display: none;
}

.cky-footer-wrapper div:not(.cky-prefrence-btn-wrapper) {
  display: none !important;
}

.cky-btn-close {
  display: none;
}

.cky-btn-reject {
  display: none;
}

.dropbtn {
  background-color: transparent;
  color: white;
  padding: 0 0.4rem 0 0.4rem;
  border: 1px white solid;
  font-weight: lighter;
  margin-top: 0.35rem;
  width: 100%;
  height: 100%;
  z-index: 4;
}

.dropbtn:hover, .dropbtn:focus {
  background-color: white;
  color: var(--dark);
}

.dropdown {
  display: inline-block;
  font-size: 1rem;
  padding-right: 1.2rem;
  white-space: nowrap;
  min-width: fit-content;
}
.dropdown ul {
  padding: 0;
}
.dropdown li {
  padding: 0;
}
.dropdown a {
  padding: 0;
}

.dropdown-content {
  display: none;
  position: absolute;
  top: 3.5rem;
  font-size: 0.9rem;
  font-weight: 500;
  background-color: white;
  z-index: 5;
  height: fit-content;
  overflow: auto;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  padding: 0;
}

.dropdown-content li {
  list-style-type: none;
}

.dropdown-content a {
  color: var(--dark);
  padding: 0.2rem 0.6rem 0.2rem 0.3rem;
  text-decoration: none;
  display: block;
}

.dropdown a:hover {
  color: white;
  background-color: var(--primary);
}

.show {
  display: block;
}

.quick-links-arrow {
  pointer-events: none;
}

.quick-links-spacer {
  padding-left: 0.5rem;
  pointer-events: none;
}

/* Skin */
#sidebar-nav .bellows.bellows-skin-vanilla {
  background: transparent;
  box-shadow: none;
}

#sidebar-nav .bellows.bellows-skin-vanilla .bellows-nav .bellows-target {
  border: none;
}

.bellows.bellows-skin-vanilla .bellows-nav .bellows-target,
.bellows.bellows-skin-vanilla .bellows-nav .bellows-custom-content {
  color: #333;
}

.bellows.bellows-skin-vanilla .bellows-nav .bellows-item-level-0 > .bellows-target,
.bellows.bellows-skin-vanilla .bellows-nav .bellows-item-level-0 > .bellows-custom-content {
  border-bottom: 1px solid rgba(0, 0, 0, 0.05);
}

.bellows.bellows-skin-vanilla .bellows-nav .bellows-item-level-0.bellows-has-custom-content > .bellows-target {
  border-bottom: none;
}

.bellows.bellows-skin-vanilla .bellows-nav .bellows-item-level-0 > .bellows-target:hover {
  background: #f2f2f2;
}

.bellows.bellows-skin-vanilla .bellows-nav .bellows-item-level-0.bellows-active > .bellows-target {
  background: #f2f2f2;
}

.bellows.bellows-skin-vanilla .bellows-nav .bellows-item-level-0.bellows-current-menu-item > .bellows-target,
.bellows.bellows-skin-vanilla .bellows-nav .bellows-item-level-0.bellows-current-menu-ancestor > .bellows-target {
  background: #f2f2f2;
}

.bellows.bellows-skin-vanilla .bellows-nav .bellows-item-level-0 > .bellows-target .bellows-subtoggle {
  background: none;
}

.bellows.bellows-skin-vanilla .bellows-nav .bellows-submenu {
  background: #fefefe;
}

.bellows.bellows-skin-vanilla .bellows-nav .bellows-submenu .bellows-target,
.bellows.bellows-skin-vanilla .bellows-nav .bellows-submenu .bellows-custom-content,
.bellows.bellows-skin-vanilla .bellows-nav .bellows-submenu .bellows-widget .textwidget {
  color: #777;
  border-bottom: 1px solid #eee;
}

.bellows.bellows-skin-vanilla .bellows-nav .bellows-submenu .bellows-has-custom-content > .bellows-target {
  border-bottom: none;
}

.bellows.bellows-skin-vanilla .bellows-nav .bellows-submenu .bellows-target:hover {
  background: #f9f9f9;
  color: #444;
}

.bellows.bellows-skin-vanilla .bellows-nav .bellows-submenu .bellows-current-menu-item > .bellows-target,
.bellows.bellows-skin-vanilla .bellows-nav .bellows-submenu .bellows-current-menu-ancestor > .bellows-target {
  background: #f9f9f9;
}

.bellows-menu-toggle.bellows-menu-toggle-skin-vanilla {
  background: #f9f9f9;
  box-shadow: 0 0px 2px rgba(0, 0, 0, 0.17);
  color: #333;
}

div.job-advert-post {
  background-color: white;
  border: var(--light) 1rem solid;
  display: flex;
  box-shadow: 5px 5px 5px #888888;
  margin-bottom: 3rem;
  height: fit-content;
}

.job-adverts-logo {
  width: 100%;
  height: 100%;
}

#twitter-section {
  background-color: var(--primary);
  height: fit-content;
  min-height: 400px;
  padding: 2rem 0;
}
#twitter-section a {
  text-decoration: none;
}
#twitter-section .twitter-card-container-container {
  position: relative;
}
#twitter-section .twitter-card-container {
  height: 100%;
  max-width: 300px;
  min-height: 0;
  min-width: 0;
}
#twitter-section .twitter-card-container a:hover {
  color: var(--primary);
}
#twitter-section .twitter-card-container .twitter-card {
  background-color: white;
  border-radius: 8px;
  height: 100%;
}
#twitter-section .twitter-card-container .twitter-card p {
  text-align: left;
}
#twitter-section .twitter-card-container .twitter-card-content-top {
  display: flex;
  height: 40px;
  gap: 1rem;
  align-items: center;
  padding-bottom: 2.5rem;
  padding-top: 1rem;
}
#twitter-section .twitter-card-container .twitter-card-content-top .twitter-user-icon-container {
  width: 3.5rem;
  height: 3.5rem;
  display: flex;
  justify-content: center;
  align-content: center;
}
#twitter-section .twitter-card-container .twitter-card-content-top .twitter-user-icon {
  width: auto;
  height: 100%;
}
#twitter-section .twitter-card-container .twitter-card-content-top .twitter-user-title {
  font-weight: bold;
  margin-bottom: 0;
}
#twitter-section .twitter-card-content {
  min-height: 330px;
  max-height: max-content;
  display: flex;
  flex-direction: column;
}
#twitter-section .twitter-card-text {
  font-size: 0.9rem;
}
#twitter-section .twitter-card {
