}

#cal-feed-instructions-container {
  height: 4px;
  transition: all 0.4s;
  transition-timing-function: ease-in-out;
  border: 4px solid var(--muted-light-light);
  position: relative;
}
#cal-feed-instructions-container .cal-feed-link-container {
  transition: all 0.4s;
  transition-timing-function: ease-in-out;
  opacity: 0;
  padding: 2rem 2rem 0rem 2rem;
}
#cal-feed-instructions-container ul {
  padding: 0;
}

#cal-feed-instructions-container.cal-feed-link-container * {
  margin-bottom: 0;
}

i#instructions-close-btn {
  position: absolute;
  top: 0.5rem;
  right: 0.5rem;
  cursor: pointer;
}

#cal-feed-instructions-container.show-instructions {
  height: 350px;
  transition: all 0.4s;
  transition-timing-function: ease-in-out;
}
#cal-feed-instructions-container.show-instructions .cal-feed-link-container {
  opacity: 1;
}

#calendar-subscribe-link-container {
  display: flex;
  align-items: center;
}
#calendar-subscribe-link-container:hover {
  cursor: pointer;
  color: var(--primary);
}

.cal-feed-link-container {
  display: flex;
}

#iCalUrl {
  border: 1px solid var(--muted-light-light);
  border-radius: 0.2rem;
}

.copy-text-btn-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 25px;
  height: 25px;
  background-color: lightgray;
  cursor: pointer;
  border-radius: 0.2rem;
  margin-left: 0.5rem;
}

.copy-text-btn-container-wrapper {
  display: inline-block;
}

div.copy-text-btn-container-wrapper * {
  margin-bottom: 0;
}

#copy-text-notification-wrapper {
  display: inline-block;
  margin-left: 0.5rem;
  opacity: 0;
  transition: all 1.5s;
  transition-timing-function: ease-out;
  padding: 0.1rem 0.2rem;
  border: 1px solid var(--muted-light-light);
  border-radius: 0.2rem;
  margin-bottom: 0;
}
#copy-text-notification-wrapper p {
  font-size: 0.7rem;
  margin-bottom: 0;
  color: var(--muted-light);
}

#copy-text-notification-wrapper.show {
  opacity: 1;
  margin-bottom: 0rem;
}

.cal-feed-instructions input, .cal-feed-instructions .copy-text-btn-container-wrapper {
  margin-bottom: 0rem;
}
.cal-feed-instructions a {
  text-decoration: none;
}
.cal-feed-instructions li {
  list-style-type: none;
}

div.categories-section * {
  margin-bottom: 0;
}

div.categories-section .categories-list {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
}
div.categories-section .category-wrapper {
  display: flex;
  align-items: center;
  gap: 0.2rem;
  font-size: 0.8rem;
  text-transform: uppercase;
}
div.categories-section .category-wrapper #categories-title {
  color: var(--muted);
}
div.categories-section .category-wrapper a {
  text-decoration: none;
  color: var(--muted);
}
div.categories-section .category-color-box {
  width: 0.5rem;
  height: 0.5rem;
}

.em-view-container + p {
  display: none;
}

.em-list #month-title {
  color: white;
  background: var(--primary);
  text-align: center;
  margin-top: 1.5rem;
  padding: 0.8rem;
  margin-bottom: 0 !important;
}

.em-event.em-item {
  display: flex;
  padding: 0.8rem 0rem;
  border-bottom: 0.4rem solid var(--muted-light-light);
}
.em-event.em-item #date {
  display: flex;
  align-items: center;
  margin-right: 1rem;
  min-width: 8rem;
  min-height: 4.2rem;
}
.em-event.em-item #date .inner-date-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0.3rem;
  width: 100%;
  height: 100%;
  position: relative;
  justify-content: center;
}
.em-event.em-item #date .inner-date-container .pointer {
  position: absolute;
  top: 35%;
  left: -0.35rem;
}
.em-event.em-item #date .inner-date-container * {
  color: white;
}

.em-item-info {
  width: 100%;
}

.event-item-info-inner {
  display: flex;
  justify-content: space-between;
}

.em-item-title {
  margin-bottom: 0 !important;
  color: var(--primary);
}

.time-container {
  display: flex;
  gap: 0.5rem;
  margin-top: 0.3rem;
}

.em-event.em-item-single {
  background: none;
  color: var(--primary);
}

