#img-links-section .mask {
  position: absolute;
  background-color: var(--overlay);
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 15rem;
  width: 100%;
  z-index: 4;
}
#img-links-section .img-link-img {
  width: 100%;
  height: 15rem;
  object-fit: cover;
